import React, { useState, useEffect } from "react";
import Sponsors from "./Sponsors";
import logo from '../logoFooter.svg';
import "./Footer.css"

function Footer() {
  const [USD, setUSD] = useState(0);
  const [EUR, setEUR] = useState(0);
  
  useEffect(() => {
    let xhr = new XMLHttpRequest();
    xhr.open('GET', 'https://iss.moex.com/iss/statistics/engines/currency/markets/selt/rates.json?iss.meta=off');
    xhr.responseType = 'json'
    xhr.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
    xhr.onload = () => {
      setUSD(xhr.response.cbrf.data[0][xhr.response.cbrf.columns.indexOf('CBRF_USD_LAST')]);
      setEUR(xhr.response.cbrf.data[0][xhr.response.cbrf.columns.indexOf('CBRF_EUR_LAST')]);
      console.log(xhr.response.cbrf.data[0][xhr.response.cbrf.columns.indexOf('CBRF_USD_LAST')]);
    }
    xhr.send()
  }, []);
  return (
    <footer>
      <div className="clouds"></div>
      <div className="footer">
        <Sponsors />
        <div className="footer-bottom">
          <div className="footer-logo">
            <a className="logo__link" href="#top">
              <img className="logo__image" src={logo} alt="Logo" />
            </a>
            <h4><a className="logo__link" href="https://cbr.ru/">Курс USD:  <span class="text-orange">{USD}</span></a></h4>
            <h4><a className="logo__link" href="https://cbr.ru/">Курс EUR:  <span class="text-orange">{EUR}</span></a></h4>
            <h4><p className="logo__link">Глушков А. В.</p></h4>
          </div>
        </div>
      </div>

    </footer>
  );
}

export default Footer;