import React from "react";
import "./PhotoFrame.css"

function Inform(props) {
    let j = props.j;

    return (
        <div id={"vulcano" + j} className="vulcano__text">
            <div className="vulcano__text--margin">
                <h2>{props.name}</h2>
            </div>
            <div className="vulcano__text--margin">
                <div className="schedule-vilcano">
                    <table>
                        <tr><th>{(j !== 0) ? "РАСПОЛОЖЕНИЕ" : ""}</th><th>{(j !== 0) ? "КООРДИНАТЫ" : ""}</th><th>{(j !== 0) ? "ВЫСОТА" : ""}</th></tr>
                        <tr><td>{props.location}</td><td>{props.coordinates}</td><td>{props.height}</td></tr>
                    </table>
                </div>
            </div>
            <div className="vulcano__text--margin">
                <h5>{props.text}</h5>
            </div>
        </div>
    );
}
export default Inform;