import React, { useEffect, useState } from 'react';
import AliceCarousel from 'react-alice-carousel';
import "react-alice-carousel/lib/alice-carousel.css";

function Gallery() {

    const [arr, setValue] = useState([]);
    let img = Object.assign([], arr);
    
    useEffect(() => {
        fetch("https://api.pexels.com/v1/collections/1qsksee", {
            headers: {
                Authorization: "NV90OXHJPT9Syi8NF5YWUOYqsBHt3rrP6usRfKVAPMvz7rFUrkpsgXnI "
            }
        })
            .then(response => response.json())
            .then(response => {
                for (let i = 0; i < (response.media).length; i++) {
                    img.push(response.media[i].src.original);
                }
                setValue(img);
            })
    }, []);
    
    const responsive = {
        0: { items: 1 },
        568: { items: 4 },
        1024: { items: 6 },
    };
    const items = [
        <div className="item" data-value="1"> <img className='gallery-item' src={arr[0]} alt={""} /> </div>,
        <div className="item" data-value="2"> <img className='gallery-item' src={arr[1]} alt={""} /> </div>,
        <div className="item" data-value="3"> <img className='gallery-item' src={arr[2]} alt={""} /> </div>,
        <div className="item" data-value="4"> <img className='gallery-item' src={arr[3]} alt={""} /> </div>,
        <div className="item" data-value="5"> <img className='gallery-item' src={arr[4]} alt={""} /> </div>,
        <div className="item" data-value="6"> <img className='gallery-item' src={arr[5]} alt={""} /> </div>,
        <div className="item" data-value="7"> <img className='gallery-item' src={arr[6]} alt={""} /> </div>,
        <div className="item" data-value="8"> <img className='gallery-item' src={arr[7]} alt={""} /> </div>,
        <div className="item" data-value="9"> <img className='gallery-item' src={arr[8]} alt={""} /> </div>,
    ];

    return (
        <div>
            <AliceCarousel
                mouseTracking
                items={items}
                responsive={responsive}
                controlsStrategy="alternate"
            />
        </div>

    );
}


export default Gallery;
  