import React, { useState } from 'react'
import Row from './Row'
import "./Schedule.css"

function Schedule() {
    const [num, setNum] = useState(0)

    return (
        <div id="schedule" className="main-schedule">
            <div className="menu-schedule">
                <div className={(num === 0) ? "event__day event__day--active" : "event__day event__day--inactive"} id="day0" onClick={() => setNum(0)}>
                    <h3>Летний тур</h3>
                    <h5 className="text--opacity">Время проведения тура: июль-сентябрь</h5>
                </div>
                <div className={(num === 1) ? "event__day event__day--active" : "event__day event__day--inactive"} id="day1" onClick={() => setNum(1)}>
                    <h3>Зимний тур</h3>
                    <h5 className="text--opacity">Время проведения тура: конец декабря - начало мая</h5>
                </div>
            </div>
            <div>
                <div id="schedule__header" className="schedule">
                    <div><h2>День       </h2></div><div></div>
                    <div><h2>Описание   </h2></div><div></div>
                    <div><h2>Места      </h2></div><div></div>
                </div>
                {[...Array(8)].map((item, counter) => <Row key={counter} j={counter + 1} n={num} />)}
            </div>
        </div>
    );
}
export default Schedule;