import React from 'react'
import Places from './Places';
import winter_json from './Winter'
import summer_json from './Summer'

function Row(row) {
    
    let n;
    let _json;
    let j = row.j - 1;
    if (row.n === 0) _json = JSON.parse(summer_json);
    if (row.n === 1) _json = JSON.parse(winter_json);
    n = (_json[j].places.length);

    return (
        <div id={"row" + j} className={(j % 8 === 7) ? "schedule row2" : "schedule row1"}>
            <div><h3>{_json[j].day}</h3><p className="text">{_json[j].lable}</p></div>
            <div></div>
            <div><h4>{_json[j].content}</h4></div>
            <div></div>
            <div>{[...Array(n)].map((item, i) => <Places key={i} image={_json[j].places[i].image} name={_json[j].places[i].name} />)}</div>
        </div>
    );
}
export default Row;