import React from "react";
import rgo from './img/rgo.svg';
import mtkk from './img/mtkk.svg';
import rostour from './img/rostour.svg';
import "./Sponsors.css"

function Sponsors() {
    return (
        <div className="main__sponsors">
        <div id="sponsors" className="sponsors">
            <div className="sponsors-item">
                <a href="https://kamgov.ru/mintur/">
                    <img className="logo-sponsor" src={mtkk} alt="Logo" />
                </a>
                <p className="text-org"><span className="text-orange">Министерство туризма</span> Камчатского края - исполнительный орган государственной власти <span className="text-orange">Камчатского края</span>, осуществляющим функции по реализации региональной политики в сфере туризма Камчатского края.</p>
            </div>
            <div className="sponsors-item">
                <a href="https://tourism.gov.ru/deyatelnost/prodvizhenie-vnutrennego-tyrizma/">
                    <img className="logo-sponsor" src={rostour} alt="Logo" />
                </a>
                <p className="text-org"><span className="text-orange">Ростуризм</span> — федеральный орган исполнительной власти, осуществляющий функции по оказанию государственных услуг, управлению государственным имуществом, а также правоприменительные функции в <span className="text-orange">сфере туризма</span> и туристской деятельности.</p>
            </div>
            <div className="sponsors-item">
                <a href="https://www.rgo.ru/">
                    <img className="logo-sponsor" src={rgo} alt="Logo" />
                </a>
                <p className="text-org"><span className="text-orange">Русское географическое общество</span> было основано по высочайшему повелению Николая I в 1845 году. В разные годы Обществом руководили представители <span className="text-orange">Российского императорского дома</span>, учёные и государственные деятели.</p>
            </div>


            </div>
        </div>
    );
}

export default Sponsors;