import React, { useState } from "react";
import PhoneInput from 'react-phone-input-2'
import "./Modal.css";
import "./Flags.css"

export const Modal = ({ closeModal }) => {
  const [count, setCount] = useState(1)
  const maxGuests = 10
  const minGuests = 1

  const handleCount = (e) => {
    if (e > maxGuests) {
      setCount(maxGuests)
    } else if (e < minGuests) {
      setCount(minGuests)
    } else setCount(e)
  }

  const decrementCount = () => {
    if (count > minGuests) setCount(count - 1)
  }

  const incrementCount = () => {
    if (count < maxGuests) setCount(count + 1)
    else if (count > maxGuests) setCount(maxGuests)
  }
  return (
    <div id="modal" className="modal">
      <div className="modal--center">
        <div className="modal-header__top">
          Отправляйтесь в путешествие мечты
        </div>
        <div className="modal-header__bottom">
          Оставьте заявку, и мы поможем подобрать вам тур
        </div>
        <div className="modal-content">
          <div className="modal-content__item">
            <input className="modal-content__input" type="text" placeholder={"Ваше имя"} />
            <PhoneInput specialLabel={''} country={'ru'}/>
            <input className="modal-content__input" type="text" placeholder={"Ваш город"} />
          </div>
          <div className="modal-content__item">
            <label className="modal-content__label">Выберите тип тура</label>
            <span className="modal-content__span">
              <input type="checkbox" className="modal-content__checkbox" name="" id="1" />
              Летний тур
            </span>
            <span className="modal-content__span">
              <input type="checkbox" className="modal-content__checkbox" name="" id="2" />
              Зимний тур
            </span>
          </div>
          <div className="modal-content__item">
            <label className="modal-content__label">Количество человек</label>
            <span>
              <button className="modal-content__btn-num" onClick={() => decrementCount()}>–</button>
              <input className="modal-content__input modal-content__input-num" type="number" value={count} onChange={(event) => { handleCount(event.target.value) }} />
              <button className="modal-content__btn-num" onClick={() => incrementCount()}>+</button>
            </span>
          </div>
          <div className="modal-content__item">
            <span className="modal-content__span">
              <input type="checkbox" className="modal-content__checkbox" name="" id="3" />
              Я согласен с политикой конфиденциальности
            </span>
          </div>

        </div>
        <div className="modal-footer">
          <button className="modal-footer__btn" onClick={closeModal}>Отправить</button>
        </div>
      </div>
    </div>
  );
};
