import React, { useState } from "react";
import "./Timer.css"

let end = new Date('06/23/2023 11:00');
let timer_s = 1000;
let timer_m = timer_s * 60;
let timer_h = timer_m * 60;
let timer_d = timer_h * 24;
let n1;
let n2;

function Timer() {
    const countdown = () => {
        let event = end - new Date();
        if (event<0) {
            end = new Date('07/04/2023 21:00');
            event = end - new Date();
            console.log(15);
        }
        let timeR = {
            d: Math.floor(event / timer_d),
            h: Math.floor((event % timer_d) / timer_h),
            m: Math.floor((event % timer_h) / timer_m),
            s: Math.floor((event % timer_m) / timer_s)
        };
        return timeR;
    };

    const [timeR, setTimer] = useState(countdown());
    setTimeout(() => { setTimer(countdown()); }, 1000);
    n1 = timeR.d % 100;
    n2 = timeR.d % 10;
    return (
        <div className="header__timer" id="timer">
            <div className="day">
                <div className="day-num">
                    {timeR.d}
                </div>
                <div className="day-text">
                    {(n1 >= 5 && n1 <= 20) ? ("Дней") : (n2 === 1) ? ("День") : (n2 >= 2 && n2 <= 4) ? ("Дня") : ("Дней")}
                </div>
                <div className="timer-text">
                    {(timeR.h / 10 < 1) ? ('0' + timeR.h) : timeR.h} {' : '}
                    {(timeR.m / 10 < 1) ? ('0' + timeR.m) : timeR.m} {' : '}
                    {(timeR.s / 10 < 1) ? ('0' + timeR.s) : timeR.s} {''}
                </div>
            </div>
        </div>
    );
}
export default Timer;