import './HeaderTop.css';
import logo from '../logo.svg';
import { useState } from "react";
import {Modal} from "../modal/Modal";

function HeaderTop() {
  const [showModal, setShowModal] = useState(false);
  return (
    <div id="top" className="header-top">
      <div className="logo">
        <a className="logo__link" href="#top">
          <img className="logo__image" src={logo} alt="Logo" />
        </a>
      </div>
      <div className="header-top__menu">
        <nav className="menu">
          <ul className="menu-list">
            <li className="menu-list__item">
              <a className="menu-list__item-link" href="#schedule">Расписсание тура</a>
            </li>
            <li className="menu-list__item">
              <a className="menu-list__item-link" href="#volcano">Вулканы</a>
            </li>
            <li className="menu-list__item">
              <a className="menu-list__item-link" href="#sponsors">Спонсоры</a>
            </li>
          </ul>
        </nav>
      </div>
      <a href="#modal" className="header-top__button btn"  onClick={() => setShowModal(true)}>Забронировать</a>
      
      <div className="burger">
        <span></span>
      </div>
      {showModal && <Modal closeModal={() => setShowModal(false)} />}
    </div>
  );
}
export default HeaderTop;