import React, { useState } from "react";
import "./PhotoFrame.css"
import Inform from "./Inform"
import volcano_json from "./Volcano";

function PhotoFrame() {
    const [foto, setFoto] = useState(0)
    let _json = JSON.parse(volcano_json);

    return (
        <div className="parent">
            <div id="volcano" className="volcano__main">
                <div className="volcano__title">
                    <h2>Вулканы Камчатки</h2>
                </div>
                <div className="content__element">
                    <div className="singers__foto">
                        <img className="img--radius shadow" id="shadow1" src="https://s16.stc.all.kpcdn.net/russia/wp-content/uploads/2020/07/Vulkan-Klyuchevskoj.jpg" alt="" />
                        <img className="img--radius shadow" id="shadow2" src="https://s16.stc.all.kpcdn.net/russia/wp-content/uploads/2020/07/Avachinskij-vulkan-1600.jpg" alt="" />
                        <img className="img--radius shadow" id="shadow3" src="https://s16.stc.all.kpcdn.net/russia/wp-content/uploads/2020/07/Vilyuchinskij-vulkan.jpg" alt="" />
                        <img className="img--radius shadow" id="shadow4" src="https://s16.stc.all.kpcdn.net/russia/wp-content/uploads/2020/07/Karymskaya-Sopka.jpg" alt="" />

                        <img className="img--radius volcano" id="volcano1" onClick={() => setFoto(1)} src="https://s16.stc.all.kpcdn.net/russia/wp-content/uploads/2020/07/Vulkan-Klyuchevskoj.jpg" alt="" />
                        <img className="img--radius volcano" id="volcano2" onClick={() => setFoto(2)} src="https://s16.stc.all.kpcdn.net/russia/wp-content/uploads/2020/07/Avachinskij-vulkan-1600.jpg" alt="" />
                        <img className="img--radius volcano" id="volcano3" onClick={() => setFoto(3)} src="https://s16.stc.all.kpcdn.net/russia/wp-content/uploads/2020/07/Vilyuchinskij-vulkan.jpg" alt="" />
                        <img className="img--radius volcano" id="volcano4" onClick={() => setFoto(4)} src="https://s16.stc.all.kpcdn.net/russia/wp-content/uploads/2020/07/Karymskaya-Sopka.jpg" alt="" />
                    </div>
                    <div className="volcano__content">
                        <Inform j={foto} name={_json[foto].name} location={_json[foto].location} coordinates={_json[foto].coordinates} height={_json[foto].height} text={_json[foto].text} />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default PhotoFrame;